.l-page-error {
    background: $color-white-2;
    min-height: 100vh;
    padding: 6rem 0;
    display: flex;
    position: relative;
    z-index: 0;
    @media (max-width: $lg) {
        padding-top: 15.7rem 0 4rem;
    }
    &__picture {
        @extend .img-cover;
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        pointer-events: none;
        z-index: -1;
        img {
            @media (max-width: $lg) {
                // height: auto;
            }
        }
    }
    &__container {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
    &__title {
        width: 100%;
        margin: 7vw 0 10rem;
        line-height: .98;
        text-align: center;
        @media (max-width: $lg) {
            margin-bottom: 3.2rem;
        }
    }
    &__btn {
        min-width: 30.3rem;
        margin-top: auto;
        @media (max-width: $md) {
            --btn-padding-y: 1.4rem;
            min-height: 4.8rem;
        }
        @media (max-width: $xs) {
            width: 100%;
            min-width: 0;
        }
    }
}